html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

/* UI */
.no-webp .inicio {
  background-image: url(../img/header.jpg); }

.webp .inicio {
  background-image: url(../img/header.webp); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .contenido-header {
        min-height: 70rem; } }

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .contenido-header h1 {
    text-align: left;
    color: #FFFFFF;
    font-weight: bold;
    line-height: 2;
    max-width: 60rem; }

.footer {
  padding: 2.5rem 0;
  background-color: #333333; }
  .footer .contenedor-footer {
    padding-top: 2.5rem; }
  .footer .copyright {
    color: #FFFFFF;
    text-align: center; }

.seccion {
  margin-bottom: 0; }

.barra {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .barra {
      flex-direction: row;
      justify-content: space-between; }
      .barra img {
        width: 30rem; } }

.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  position: relative;
  top: .3rem; }

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row; }
    .navegacion a {
      margin-right: 2rem;
      font-size: 2rem; }
      .navegacion a:last-of-type {
        margin-right: 0; }
      .navegacion a:hover {
        color: #E08709;
        text-shadow: 0px 0px 1px #E08709; } }

@media (min-width: 768px) {
  .iconos-nosotros {
    margin: 10rem 0;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(3, 1fr); } }

.icono img {
  height: 10rem; }

.icono h3 {
  text-transform: uppercase; }
